<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Tipo de Cesta</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="tipoCesta.id"
            label="Id"
            placeholder="Id do Tipo da Cesta"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="tipoCesta.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome do Tipo da Cesta"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-flex" cols="12">
          <v-textarea
          outlined
          v-model="tipoCesta.descricao"
          label="Descrição"
          placeholder="Descrição do Tipo da Cesta"
        ></v-textarea>
        </v-col>
      </v-row>
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/TipoCestaDataService";

export default {
  name: "TiposCestas",
  data() {
      return {
          tipoCesta: {id:0, nome:'', descricao:''},
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.tipoCesta = response.data
              });
          }
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.tipoCesta, {
                  id: this.id,
                  tipoCesta: this.tipoCesta
              }).then(() => {
                  console.log(this.tipoCesta)
                  this.$router.push('/tipos-cestas')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.tipoCesta);
              DataService.create(this.tipoCesta, {
                  tipoCesta: this.tipoCesta
              }).then(() => {
                  console.log(this.tipoCesta)
                  this.$router.push('/tipos-cestas')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

